
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'

export default {
components: {
Cell,
Group,
XTextarea
},
data() {
return {
idArr:[],

rejectInfo: {
name: '',
mobile: '18674087602',
address:
' '
},

skuInfos: [
{
title: '',
detail: 'asdadqwdqdq',
price: '150.00',
num: 10
}
],

payInfo:{
payPrice:'1500.00'
},

userPonnts:'',
}
},
mounted(){

this.idArr = this.$route.query.skuIds

this.createDefault()
},
methods: {
toAddress() {
this.$router.push({ path: '/addresslist' })
},
createDefault(){

let skuInfos = []
for(let i in this.idArr){
skuInfos.push(this.skuInfos[0])
}
this.skuInfos = skuInfos
}
}
}



